// this includes endpoints at data.nba.com
var getJson = require("./get-json");

var _require = require("./util/string"),
    interpolate = _require.interpolate;

var scoreboardURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/scoreboard/__date__/games.json");
var boxScoreURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/game/__date__/__gameId__/boxscore.json");
var playByPlayURL = interpolate("http://data.nba.com/data/5s/json/cms/noseason/game/__date__/__gameId__/pbp_all.json");
var scheduleURL = interpolate("http://data.nba.com/data/10s/v2015/json/mobile_teams/nba/__season__/league/00_full_schedule.json"); // NOTE: the 'date' argument should be a string in format like "20181008" (which indicates Oct 8 2018)
// You *can* pass a Date object but beware of timezone weirdness!

var scoreboard = function scoreboard(date) {
  return getJson(scoreboardURL({
    date: dateToYYYYMMDD(date)
  }));
};

scoreboard.defaults = {
  date: null
};

var boxScore = function boxScore(date, gameId) {
  return getJson(boxScoreURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

boxScore.defaults = {
  date: null,
  gameId: null
};

var playByPlay = function playByPlay(date, gameId) {
  return getJson(playByPlayURL({
    date: dateToYYYYMMDD(date),
    gameId: gameId
  }));
};

playByPlay.defaults = {
  date: null,
  gameId: null
}; // NOTE: the 'season' argument is the first year of the NBA season e.g. "2018" for the 2018-19 season
// 2015 is the first valid season 

var schedule = function schedule(season) {
  return getJson(scheduleURL({
    season: season
  }));
};

schedule.defaults = {
  season: null
};

function dateToYYYYMMDD(date) {
  if (date instanceof Date) {
    return [date.getFullYear(), String(date.getMonth() + 1).padStart(2, 0), String(date.getDate()).padStart(2, 0)].join("");
  } // TODO: better checking here?


  return date;
}

module.exports = {
  scoreboard: scoreboard,
  boxScore: boxScore,
  playByPlay: playByPlay,
  schedule: schedule
};